* {
    box-sizing: border-box;
  }
  html {
    height: 100%;
    line-height: 1.15;
    text-size-adjust: 100%;
  }
  body {
    height: 100%;
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: "MMTextProWebTT-Regular", Helvetica, Arial, sans-serif;
  }
  h1, h2, h3, h4, h5 {
    font-family: "MMHeadlineWebTT-Regular", "MMHeadline-regular", Helvetica, Arial, sans-serif !important;
  }
  a{
    text-decoration: none !important;
  }
  .titleCenter{
    text-align: center;
    margin: auto;
    font-size: 44px;
    margin-bottom: 30px;
  }
  .imgCenter{
    max-width: 600px;
    width: 100%;
    margin: auto;
    display: flex;
  }

  
